import React from 'react';
import ReactDOM from 'react-dom/client';
import { createBrowserRouter, RouterProvider } from 'react-router-dom';

import { store } from './store/index';
import { Provider } from 'react-redux';

import './styles/index.css';
import HomePage from './pages/home-page/home-page';
import ClientsPage from './pages/clients-page/clients-page';
import ErrorPage from './pages/error-page/error-page';
import ClientPage from './pages/client-page/client-page';

import LoginPage from './pages/LoginPage/LoginPage';
import RegisterPage from './pages/RegisterPage/RegisterPage';

import './firebase';
import LoginGooglePage from './pages/LoginGooglePage/LoginGooglePage';
import AdminClientPage from './pages/admin-client-page/admin-client-page';

const router = createBrowserRouter([
  {
    path: '/smile',
    element: <HomePage />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: '/smile',
        element: <ClientsPage />,
      },
      {
        path: '/smile/client/:userId',
        element: <ClientPage />,
      },
      {
        path: '/smile/admin-client/:userId',
        element: <AdminClientPage />,
      },
      {
        path: '/smile/login',
        element: <LoginGooglePage />,
      },
      {
        path: '/smile/login-for-tst',
        element: <LoginPage />,
      },
      {
        path: '/smile/register',
        element: <RegisterPage />,
      },
    ],
  },
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <Provider store={store}>
    <RouterProvider router={router} />
  </Provider>,
);
