import React, { useState } from 'react';

import styles from './admin-client-page.module.css';
import { useIfAdmin } from '../../hooks/use-auth';
import { Navigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import AdminClientInfo from '../../components/AdminClientInfo/AdminClientInfo';
import AdminClientControl from '../../components/AdminClientControl/AdminClientControl';
import Textarea from '../../components/Textarea/Textarea';
import AdminClientBody from '../../components/AdminClientBody/AdminClientBody';
import { updataClient } from '../../firebase';
import { updataBodyVerify } from '../../store/slices/clientsSlice';

const AdminClientPage = () => {
  const dispatch = useDispatch();
  const { userId } = useParams();
  //    
  const [bodyItems, setBodyItems] = useState(false);

  const clientsBodyData = useSelector((state) => state.clientBodyData.clientBodyData);
  const clients = useSelector((state) => state.clients.clients);
  const clientArray = clients.filter((item) => item.g_localId === userId);
  const client = clientArray[0];

  //    localStorage  ClientsPage
  localStorage.setItem('scrollY', window.scrollY);

  const ifOpenBodyItems = (user) => {
    setBodyItems(!bodyItems);

    //         
    const date = new Date().getTime();
    updataClient({ bodyVerify: date }, user.g_localId);
    dispatch(updataBodyVerify({ userId: user.g_localId, date: date }));
  };

  // =================================================================================

  return useIfAdmin() ? (
    <div className="page">
      <div className="container">
        <div className={styles.clientPage_wrapper}>
          <h3 className={styles.form_title}> </h3>

          <AdminClientInfo name={client.name} tel={client.tel} />

          <AdminClientControl
            userId={client.g_localId}
            groupAccompany={client.groupAccompany}
            individualAccompany={client.individualAccompany}
            calculated={client.calculated}
          />

          <Textarea notes={client.notes} userId={client.g_localId} />
          {/* ---------------------------------------------------------- */}

          <div className={styles.show_body_btn_wrapper}>
            <div className={styles.body_count}>
              {clientsBodyData.length ? clientsBodyData.length : '-'}
            </div>
            <button
              type="button"
              className={
                bodyItems ? styles.show_body_btn + ' ' + styles.open : styles.show_body_btn
              }
              onClick={() => ifOpenBodyItems(client)}>
              <span> </span>
              <svg viewBox="0 0 15 15" width="14px" height="14px">
                <path d="M7.158.3.29 6.825a.929.929 0 0 0 0 1.349L7.158 14.7c.422.4 1.087.4 1.508 0a1.07 1.07 0 0 0 0-1.554L2.913 7.68a.247.247 0 0 1 0-.361l5.753-5.466a1.07 1.07 0 0 0 0-1.554 1.096 1.096 0 0 0-1.508 0Z"></path>
              </svg>
            </button>
            {client.bodyVerify < client.bodyUpdata ? (
              <span className={styles.compare_btn_active}></span>
            ) : (
              ''
            )}
          </div>

          {bodyItems ? <AdminClientBody email={client.g_email} /> : ''}

          {/* ---------------------------------------------------------- */}
        </div>
      </div>
    </div>
  ) : (
    <Navigate to="/smile/login" />
  );
};

export default AdminClientPage;
