import React, { Fragment, useEffect, useState } from 'react';

import styles from './PopupCompare.module.css';
import { bodyScrollOverflowHidden } from '../../helper';
import { getBodyForCompare } from '../../firebase';
import LoaderPage from '../LoaderPage/LoaderPage';

const getCompareItems = (compareItems) => {
  return compareItems.map((item) => {
    return (
      <Fragment key={item.time_milliseconds}>
        <div className={item.date ? styles.body_element : ''}>{item.date ? item.date : ''}</div>
        <div className={item.date ? styles.body_element : ''}>
          {item.date_time ? item.date_time : ''}
        </div>
        <div className={styles.body_element}>{item.kg}</div>
        <div className={styles.body_element}>{item.breastVolumes}</div>
        <div className={styles.body_element}>{item.handVolumes}</div>
        <div className={styles.body_element}>{item.waistVolumes}</div>
        <div className={styles.body_element}>{item.abdominalVolumes}</div>
        <div className={styles.body_element}>{item.thighVolumes}</div>
        <div className={styles.body_element}>{item.legVolumes}</div>
      </Fragment>
    );
  });
};
const getCompareItemsValue = (compareItems) => {
  return compareItems.map((item) => {
    return (
      <Fragment key={item.id}>
        <div></div>
        <div></div>
        <div
          className={
            item.kg > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.kg > 0
            ? '-' + item.kg.toFixed(2) + ' ' + ''
            : '+' + Math.abs(item.kg.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.breastVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.breastVolumes > 0
            ? '-' + item.breastVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.breastVolumes.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.handVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.handVolumes > 0
            ? '-' + item.handVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.handVolumes.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.waistVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.waistVolumes > 0
            ? '-' + item.waistVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.waistVolumes.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.abdominalVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.abdominalVolumes > 0
            ? '-' + item.abdominalVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.abdominalVolumes.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.thighVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.thighVolumes > 0
            ? '-' + item.thighVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.thighVolumes.toFixed(2)) + ' ' + ''}
        </div>
        <div
          className={
            item.legVolumes > 0
              ? styles.body_element + ' ' + styles.plus
              : styles.body_element + ' ' + styles.minus
          }>
          {item.legVolumes > 0
            ? '-' + item.legVolumes.toFixed(1) + ' ' + ''
            : '+' + Math.abs(item.legVolumes.toFixed(2)) + ' ' + ''}
        </div>
      </Fragment>
    );
  });
};
const getCompareValue = (compareItems) => {
  let values = [
    {
      id: compareItems[0].id + compareItems[1].id,
      kg: compareItems[0].kg - compareItems[1].kg,
      breastVolumes: compareItems[0].breastVolumes - compareItems[1].breastVolumes,
      handVolumes: compareItems[0].handVolumes - compareItems[1].handVolumes,
      waistVolumes: compareItems[0].waistVolumes - compareItems[1].waistVolumes,
      abdominalVolumes: compareItems[0].abdominalVolumes - compareItems[1].abdominalVolumes,
      thighVolumes: compareItems[0].thighVolumes - compareItems[1].thighVolumes,
      legVolumes: compareItems[0].legVolumes - compareItems[1].legVolumes,
    },
  ];

  return values;
};

export const PopupCompare = (props) => {
  const [ompareArrey, setCompareArrey] = useState([]);
  const [loaderPage, setLoaderPage] = useState(false);

  //    body
  bodyScrollOverflowHidden();

  const getBody = async (email) => {
    const ompareArreyBD = await getBodyForCompare(email);
    //reverse()     ,   .
    setCompareArrey(ompareArreyBD.reverse());
    setLoaderPage(false);
  };

  useEffect(() => {
    if (!props.ompareArrey.length) {
      setLoaderPage(true);

      getBody(props.email);
    }
  }, []);

  return (
    <div className={styles.fon}>
      <button
        className={styles.close_popup_btm}
        type="button"
        onClick={() => props.closedPopupCompare()}>
        <svg viewBox="-180.598 -180.697 425.197 425.197" width="10px" height="10px">
          <path d="m81.793 31.902 139.404-139.404a35.114 35.114 0 0 0 10.304-24.891c0-19.445-15.76-35.206-35.206-35.206-9.717 0-18.518 3.943-24.891 10.315L32-17.891l-139.404-139.392c-6.372-6.372-15.174-10.315-24.891-10.315-19.445 0-35.206 15.761-35.206 35.206 0 9.717 3.943 18.518 10.315 24.891L-17.793 31.902l-139.392 139.404c-6.372 6.372-10.315 15.174-10.315 24.891 0 19.445 15.761 35.206 35.206 35.206 9.717 0 18.518-3.943 24.891-10.315L32 81.694l139.404 139.404c6.372 6.361 15.174 10.304 24.891 10.304 19.445 0 35.206-15.76 35.206-35.206 0-9.717-3.943-18.518-10.315-24.891L81.793 31.902z"></path>
        </svg>
      </button>
      {props.ompareArrey.length || ompareArrey.length ? (
        <div className={styles.compare_wrapper}>
          <div className={styles.body_item_title}></div>
          <div className={styles.body_item_title}></div>
          <div className={styles.body_item_title}></div>
          <div className={styles.body_item_title}>' </div>
          <div className={styles.body_item_title}>' </div>
          <div className={styles.body_item_title}>' </div>
          <div className={styles.body_item_title}>' </div>
          <div className={styles.body_item_title}>' </div>
          <div className={styles.body_item_title}>' </div>

          {props.ompareArrey.length ? getCompareItems(props.ompareArrey) : ''}
          {props.ompareArrey.length > 1
            ? getCompareItemsValue(getCompareValue(props.ompareArrey))
            : ''}
          {ompareArrey.length ? getCompareItems(ompareArrey) : ''}
          {ompareArrey.length > 1 ? getCompareItemsValue(getCompareValue(ompareArrey)) : ''}
        </div>
      ) : loaderPage ? (
        <LoaderPage />
      ) : (
        ' :)'
      )}
    </div>
  );
};
