import React from 'react';

import styles from './RestartBtn.module.css';

const RestartBtn = ({ handleClick, clientsLength }) => {
  return (
    <div className={styles.restart_wrapper} onClick={() => handleClick('restart')}>
      <button>Restart</button>
      <span>{clientsLength}</span>
    </div>
  );
};

export default RestartBtn;
