import { configureStore } from '@reduxjs/toolkit';
import userReducer from './slices/userSlice';
import clientBodyDataReducer from './slices/clientBodyDataSlice';
import setCliensDataReducer from './slices/clientsSlice';

export const store = configureStore({
  reducer: {
    user: userReducer,
    clientBodyData: clientBodyDataReducer,
    clients: setCliensDataReducer,
  },
});
