import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';

import { initializeApp } from 'firebase/app';
import { getFirestore } from 'firebase/firestore';
import { collection, addDoc } from 'firebase/firestore';
import { app, updataClient } from '../../firebase';

import styles from './PopupAddBodyItem.module.css';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden } from '../../helper';
import Alert from '../Alert/Alert';

const closePopup = (setAddBodyItem) => {
  setAddBodyItem(false);
  bodyScrollOverflowAuto();
};

export const PopupAddBodyItem = (props) => {
  // console.log('PopupAddBodyItem render');
  const { g_email, g_localId, name } = useSelector((state) => state.user);

  //    
  const [alertSetBodyTrue, setAlertSetBodyTrue] = useState(false);

  const [alertError, setAlertError] = useState(false);

  //    body
  bodyScrollOverflowHidden();

  //   
  const pushData = async (clientBodyData, form) => {
    const db = getFirestore(app);
    try {
      await addDoc(collection(db, 'clientsBodyData'), clientBodyData);
      //     
      updataClient({ bodyUpdata: new Date().getTime() }, g_localId);
      //  
      form.reset();
      setAlertSetBodyTrue(true);
    } catch (e) {
      setAlertSetBodyTrue(false);

      setAlertError(true);
      console.error('Error adding document: ', e);
    }
  };

  const formSubmit = (event) => {
    event.preventDefault();

    const formData = new FormData(event.target);

    const clientBodyData = {
      date: new Date().toLocaleDateString(), //   
      date_time: new Date().toLocaleTimeString(), //   
      date_day: new Date().getDay(), //   
      time_milliseconds: new Date().getTime(), //    1  1970 
      nume: name,
      g_localId: g_localId,
      g_email: g_email,
      image_url: '',
      kg: formData.get('kg'),
      breastVolumes: formData.get('breastVolumes'),
      handVolumes: formData.get('handVolumes'),
      waistVolumes: formData.get('waistVolumes'),
      abdominalVolumes: formData.get('abdominalVolumes'),
      thighVolumes: formData.get('thighVolumes'),
      legVolumes: formData.get('legVolumes'),
    };
    pushData(clientBodyData, event.target);
  };

  return (
    <>
      <div className={styles.fon}>
        <button
          className={styles.close_popup_btm}
          type="button"
          onClick={() => closePopup(props.setAddBodyItem)}>
          <svg viewBox="-180.598 -180.697 425.197 425.197" width="10px" height="10px">
            <path d="m81.793 31.902 139.404-139.404a35.114 35.114 0 0 0 10.304-24.891c0-19.445-15.76-35.206-35.206-35.206-9.717 0-18.518 3.943-24.891 10.315L32-17.891l-139.404-139.392c-6.372-6.372-15.174-10.315-24.891-10.315-19.445 0-35.206 15.761-35.206 35.206 0 9.717 3.943 18.518 10.315 24.891L-17.793 31.902l-139.392 139.404c-6.372 6.372-10.315 15.174-10.315 24.891 0 19.445 15.761 35.206 35.206 35.206 9.717 0 18.518-3.943 24.891-10.315L32 81.694l139.404 139.404c6.372 6.361 15.174 10.304 24.891 10.304 19.445 0 35.206-15.76 35.206-35.206 0-9.717-3.943-18.518-10.315-24.891L81.793 31.902z"></path>
          </svg>
        </button>
        <div className={styles.popup_wrapper}>
          <form className={styles.formInner} onSubmit={(event) => formSubmit(event)}>
            <h3 className={styles.form_title}>  </h3>
            <input
              className={styles.form_input}
              type="number"
              placeholder=" ()"
              name="kg"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="breastVolumes"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="handVolumes"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="waistVolumes"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="abdominalVolumes"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="thighVolumes"
              required></input>
            <input
              className={styles.form_input}
              type="number"
              placeholder="'  ()"
              name="legVolumes"
              required></input>

            <button className={styles.form_btn} type="submit">
              
            </button>
          </form>
        </div>
        {alertSetBodyTrue ? (
          <div className={styles.alert_wrapper}>
            <div className={styles.alert}>
              <div></div>
              <button
                className={styles.alert_close_btn}
                onClick={() => closePopup(props.setAddBodyItem)}>
                
              </button>
            </div>
          </div>
        ) : (
          ''
        )}
      </div>
      {alertError && <Alert text="   !" handleClick={setAlertError} />}
    </>
  );
};
