import React from 'react';

import styles from './AdminOrClient.module.css';
import { useSelector } from 'react-redux';

const AdminOrClient = ({ handleClick }) => {
  const userId = useSelector((state) => state.user.g_localId);
  return (
    <div className={styles.content_wrapper}>
      <div className={styles.buttons_wrapper}>
        <button className={styles.button} onClick={() => handleClick('admin')}>
          i  <span></span>
        </button>
        <button className={styles.button} onClick={() => handleClick('user', userId)}>
            <span></span>
        </button>
      </div>
    </div>
  );
};

export default AdminOrClient;
