import React, { useState } from 'react';

import styles from './ompareBtn.module.css';
import { PopupCompare } from '../PopupCompare/PopupCompare';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden } from '../../helper';
import { updataClient } from '../../firebase';
import { useDispatch } from 'react-redux';
import { updataBodyVerify } from '../../store/slices/clientsSlice';
import LoaderPage from '../LoaderPage/LoaderPage';

const ompareBtn = ({ user }) => {
  const dispatch = useDispatch();
  const [popupCompare, setPopupCompare] = useState(false);
  const [loaderPage, setLoaderPage] = useState(false);

  const closedPopupCompare = () => {
    bodyScrollOverflowAuto();
    //    
    setPopupCompare(false);
  };

  const showCompare = () => {
    setLoaderPage(true);
    const date = new Date().getTime();
    updataClient({ bodyVerify: date }, user.g_localId);
    dispatch(updataBodyVerify({ userId: user.g_localId, date: date }));
    setPopupCompare(true);
    bodyScrollOverflowHidden();
    setLoaderPage(false);
  };

  return (
    <>
      <button className={styles.compare_btn} onClick={() => showCompare()}>
        {user.bodyVerify < user.bodyUpdata && <span className={styles.compare_btn_active}></span>}
      </button>
      {popupCompare && !loaderPage && (
        <PopupCompare
          closedPopupCompare={closedPopupCompare}
          ompareArrey={[]}
          email={user.g_email}
        />
      )}
      {loaderPage && <LoaderPage />}
    </>
  );
};

export default ompareBtn;

