import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import styles from './Header.module.css';
import { useAuth } from '../../hooks/use-auth';
import { setUserDataRedux } from '../../store/slices/userSlice';
import { updataClient } from '../../firebase';
import LoaderPage from '../LoaderPage/LoaderPage';
import FormAddData from '../FormAddData/FormAddData';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden, phoneCorrect } from '../../helper';

export const Header = () => {
  const dispatch = useDispatch();

  const [menu, setMenu] = useState(false);
  const [updateForm, setUpdateForm] = useState(false);
  const [loader, setLoader] = useState(false);

  const { g_email, name, tel, g_localId, groupAccompany, individualAccompany, calculated } =
    useSelector((state) => state.user);

  const showMenu = () => {
    setMenu(true);
    bodyScrollOverflowHidden();
  };
  const closeMenu = () => {
    bodyScrollOverflowAuto();
    setMenu(false);
  };

  //  
  const updateData = async ({ name, tel }) => {
    setLoader(true);

    const data = {
      name: name,
      tel: tel,
    };

    await updataClient(data, g_localId);

    dispatch(setUserDataRedux(data));
    setLoader(false);
    setUpdateForm(false);
  };

  return (
    useAuth() && (
      <div className={styles.header}>
        <div className="container">
          <div className={styles.header_wrapper}>
            <div>:)</div>
            <div className={styles.user_btn_wrapper} onClick={() => showMenu()}>
              <svg viewBox="0 0 24 24" width="24px" height="24px">
                <path d="M17 7A5 5 0 1 1 7 7a5 5 0 0 1 10 0Zm-5 3a3 3 0 1 0 0-6 3 3 0 0 0 0 6ZM4.023 15.987C5.514 14.317 7.993 13 12 13c4.006 0 6.486 1.316 7.977 2.987 1.461 1.636 1.863 3.488 1.976 4.393.118.94-.648 1.62-1.453 1.62h-17c-.805 0-1.571-.68-1.454-1.62.113-.905.515-2.756 1.977-4.393ZM4.144 20h15.712c-.174-.744-.553-1.765-1.37-2.681C17.43 16.137 15.527 15 12 15c-3.529 0-5.43 1.137-6.486 2.319-.818.916-1.196 1.937-1.37 2.681Z"></path>
              </svg>
            </div>
          </div>
        </div>

        <div
          className={
            menu ? styles.user_menu_wrapper + ' ' + styles.showMenu : styles.user_menu_wrapper
          }>
          <div
            className={
              menu ? styles.user_menu_content + ' ' + styles.showMenu : styles.user_menu_content
            }>
            <button className={styles.close_btn} type="button" onClick={() => closeMenu()}>
              <svg viewBox="-180.598 -180.697 425.197 425.197" width="10px" height="10px">
                <path d="m81.793 31.902 139.404-139.404a35.114 35.114 0 0 0 10.304-24.891c0-19.445-15.76-35.206-35.206-35.206-9.717 0-18.518 3.943-24.891 10.315L32-17.891l-139.404-139.392c-6.372-6.372-15.174-10.315-24.891-10.315-19.445 0-35.206 15.761-35.206 35.206 0 9.717 3.943 18.518 10.315 24.891L-17.793 31.902l-139.392 139.404c-6.372 6.372-10.315 15.174-10.315 24.891 0 19.445 15.761 35.206 35.206 35.206 9.717 0 18.518-3.943 24.891-10.315L32 81.694l139.404 139.404c6.372 6.361 15.174 10.304 24.891 10.304 19.445 0 35.206-15.76 35.206-35.206 0-9.717-3.943-18.518-10.315-24.891L81.793 31.902z"></path>
              </svg>
            </button>

            <div className={styles.user_info_wrapper}>
              {g_email && <div>{g_email}</div>}

              <div className={styles.user_groupAccompany_wrapper}>
                {!groupAccompany && !individualAccompany && (
                  <div className={styles.user_groupAccompany_false}>   </div>
                )}

                {groupAccompany && <div>  </div>}
                {individualAccompany && <div> </div>}

                {calculated ? (
                  <div></div>
                ) : individualAccompany || groupAccompany ? (
                  <div className={styles.user_groupAccompany_false}> </div>
                ) : (
                  ''
                )}
              </div>
            </div>

            {name ? <div>': {name} </div> : <div>':   </div>}
            {tel ? <div>: {phoneCorrect(tel)}</div> : <div>:   </div>}
            <button className={styles.update_btn} onClick={() => setUpdateForm(true)}>
              
            </button>
          </div>
        </div>

        {updateForm && (
          <div className={styles.update_form_wrapper}>
            <button className={styles.close_btn} type="button" onClick={() => setUpdateForm(false)}>
              <svg viewBox="-180.598 -180.697 425.197 425.197" width="10px" height="10px">
                <path d="m81.793 31.902 139.404-139.404a35.114 35.114 0 0 0 10.304-24.891c0-19.445-15.76-35.206-35.206-35.206-9.717 0-18.518 3.943-24.891 10.315L32-17.891l-139.404-139.392c-6.372-6.372-15.174-10.315-24.891-10.315-19.445 0-35.206 15.761-35.206 35.206 0 9.717 3.943 18.518 10.315 24.891L-17.793 31.902l-139.392 139.404c-6.372 6.372-10.315 15.174-10.315 24.891 0 19.445 15.761 35.206 35.206 35.206 9.717 0 18.518-3.943 24.891-10.315L32 81.694l139.404 139.404c6.372 6.361 15.174 10.304 24.891 10.304 19.445 0 35.206-15.76 35.206-35.206 0-9.717-3.943-18.518-10.315-24.891L81.793 31.902z"></path>
              </svg>
            </button>
            <FormAddData
              title=""
              userName={name}
              tel={tel}
              handleClick={updateData}
              titleIgnor={''}
              ignorClick={(value) => setUpdateForm(!value)}
            />
            {loader && <LoaderPage />}
          </div>
        )}
      </div>
    )
  );
};
