import styles from './Alert.module.css';
const Alert = ({ text, handleClick }) => {
  return (
    <div className={styles.alert_fon}>
      <div className={styles.alert_wrapper}>
        <p className={styles.alert_text}>{text}</p>
        <button className={styles.alert_close_btn} onClick={() => handleClick(false)}>
          
        </button>
      </div>
    </div>
  );
};

export default Alert;
