import React, { useState } from 'react';
import styles from './Textarea.module.css';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden } from '../../helper';
import { updataClient } from '../../firebase';

const Textarea = ({ notes, userId }) => {
  //   textarea  
  const [showTextarea, setShowTextarea] = useState(false);
  //   
  const [textarea, setTextarea] = useState(notes);
  //   
  const [textareaOnchange, setTextareaOnchange] = useState(false);

  const controlTextarea = () => {
    if (showTextarea) {
      bodyScrollOverflowAuto();
    } else {
      bodyScrollOverflowHidden();
    }
    setShowTextarea(!showTextarea);
  };

  const onChangeTextarea = (e) => {
    setTextarea(e.target.value);
    if (!textareaOnchange) {
      setTextareaOnchange(true);
    }
  };

  const saveTextarea = async () => {
    setTextareaOnchange(false);
    setShowTextarea(false);
    updataClient({ notes: textarea }, userId);
    bodyScrollOverflowAuto();
  };

  return (
    <div
      className={
        showTextarea ? styles.textarea_wrapper + ' ' + styles.big : styles.textarea_wrapper
      }>
      {showTextarea ? (
        <textarea
          rows="4"
          className={styles.textarea}
          placeholder="..."
          defaultValue={textarea}
          onChange={(e) => onChangeTextarea(e)}></textarea>
      ) : (
        <textarea
          readOnly
          rows="4"
          className={styles.textarea}
          placeholder="..."
          defaultValue={textarea}></textarea>
      )}

      {textareaOnchange && showTextarea ? (
        <div className={styles.textarea_btn_save} onClick={() => saveTextarea()}>
          
        </div>
      ) : (
        <div className={styles.textarea_btn} onClick={() => controlTextarea()}></div>
      )}
    </div>
  );
};

export default Textarea;
