import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';

import { useDispatch } from 'react-redux';
import { setUser } from '../store/slices/userSlice';
import { useNavigate } from 'react-router-dom';
import FormRegisterAndLogin from './FormRegisterAndLogin/FormRegisterAndLogin';

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handlerLogin = (event, email, password) => {
    event.preventDefault();

    const auth = getAuth();
    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        dispatch(
          setUser({
            g_email: user.email,
            g_id: user.uid,
          }),
        );
        if (user.uid === process.env.REACT_APP_FIREBASE_ADMIN_UID) {
          navigate('/clients');
        } else {
          navigate('/client/1');
        }
      })
      .catch((error) => {
        // alert('  !!');
        console.error(error);

        // const errorCode = error.code;
        // const errorMessage = error.message;
      });
  };
  return <FormRegisterAndLogin title="" handleClick={handlerLogin} />;
};

export default Login;
