import { useState } from 'react';

import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';

import styles from './FormAddData.module.css';

const FormAddData = ({
  title,
  handleClick,
  ignorClick,
  userName = '',
  tel = '',
  titleIgnor = '',
}) => {
  const [name, setName] = useState(userName);
  const [phoneNumber, setPhoneNumber] = useState(tel);

  const [nameError, setNameError] = useState(false);
  const [telError, setTelError] = useState(false);

  const sendNameAndTel = () => {
    if (name.trim().length < 3) {
      setNameError(true);
      return;
    } else {
      setNameError(false);
    }
    if (phoneNumber.trim().length < 12) {
      setTelError(true);
      return;
    } else {
      setTelError(false);
    }
    handleClick({ name: name.trim(), tel: phoneNumber.trim() });
  };

  return (
    <div>
      <div className={styles.formInner}>
        <span className={styles.ignor_btn} onClick={() => ignorClick(true)}>
          {titleIgnor}
        </span>

        <div className={styles.input_wrapper}>
          {nameError ? <div className={styles.error}>'      </div> : ''}
          <input
            className={styles.form_input}
            type="text"
            required
            name="name"
            placeholder=""
            defaultValue={name}
            onChange={(e) => setName(e.target.value)}
          />
          <div className={styles.input_title}>'</div>
        </div>
        <div className={styles.input_wrapper}>
          {telError ? <div className={styles.error}>     12 </div> : ''}
          <PhoneInput
            country={'ua'}
            value={phoneNumber}
            onChange={setPhoneNumber}
            inputProps={{ required: true }}
            placeholder={'+380 ( 96 ) 777 77 77'}
          />
        </div>
        <button className={styles.form_btn} onClick={() => sendNameAndTel()}>
          {title}
        </button>
      </div>
    </div>
  );
};

export default FormAddData;
