import { useState } from 'react';
import styles from './Search.module.css';
import { searchClients, ifSearch } from '../../store/slices/clientsSlice';
import { useDispatch } from 'react-redux';

const Search = () => {
  const dispatch = useDispatch();
  const [search, setSerch] = useState(false);
  const [input, setInput] = useState('');

  const onChangeInput = (e) => {
    const inputValue = e.target.value;
    setInput(inputValue);
    if (inputValue.trim().length) {
      dispatch(ifSearch(true));
      dispatch(searchClients(inputValue));
    } else {
      dispatch(ifSearch(false));
    }
  };

  const showSearch = (boolean) => {
    if (boolean) {
      setSerch(true);
    } else {
      setInput('');
      setSerch(false);
      dispatch(ifSearch(false));
    }
  };

  console.log(input);
  return (
    <div className={search ? styles.search + ' ' + styles.active : styles.search}>
      <button className={styles.search__btn} onClick={() => showSearch(true)}>
        <svg viewBox="0 0 24 24" width="24px" height="24px" fill="#fff">
          <path
            fillRule="evenodd"
            clipRule="evenodd"
            d="M13.753 13.753a1.25 1.25 0 0 1 1.768 0l6 6a1.25 1.25 0 1 1-1.768 1.768l-6-6a1.25 1.25 0 0 1 0-1.768Z"></path>
          <path
            fillRule="evenodd"
            clipRule="evenodd"
            d="M9.75 4.25a5.5 5.5 0 1 0 0 11 5.5 5.5 0 0 0 0-11Zm-8 5.5a8 8 0 1 1 16 0 8 8 0 0 1-16 0Z"></path>
        </svg>
      </button>
      <div className={styles.search__block}>
        <input
          className={styles.search__input}
          placeholder=" ' "
          value={input}
          type="text"
          onChange={(e) => onChangeInput(e)}></input>
        <button className={styles.search__btn_search} onClick={() => showSearch(false)}>
          <svg viewBox="-180.598 -180.697 425.197 425.197" width="16px" height="16px">
            <path d="m81.793 31.902 139.404-139.404a35.114 35.114 0 0 0 10.304-24.891c0-19.445-15.76-35.206-35.206-35.206-9.717 0-18.518 3.943-24.891 10.315L32-17.891l-139.404-139.392c-6.372-6.372-15.174-10.315-24.891-10.315-19.445 0-35.206 15.761-35.206 35.206 0 9.717 3.943 18.518 10.315 24.891L-17.793 31.902l-139.392 139.404c-6.372 6.372-10.315 15.174-10.315 24.891 0 19.445 15.761 35.206 35.206 35.206 9.717 0 18.518-3.943 24.891-10.315L32 81.694l139.404 139.404c6.372 6.361 15.174 10.304 24.891 10.304 19.445 0 35.206-15.76 35.206-35.206 0-9.717-3.943-18.518-10.315-24.891L81.793 31.902z"></path>
          </svg>
        </button>
      </div>
    </div>
  );
};

export default Search;
