import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  clients: [],
  clientsSearch: [],
  scrollY: 0,
  search: false,
};

const clientsSlice = createSlice({
  name: 'clients',
  initialState,
  reducers: {
    setCliensData(state, { payload }) {
      state.clients = payload;
    },

    setScrollY(state, { payload }) {
      state.scrollY = payload;
    },
    searchClients(state, { payload }) {
      state.clientsSearch = state.clients.filter((item) =>
        item.name.toLowerCase().trim().includes(payload.trim().toLowerCase()),
      );
    },

    ifSearch(state, { payload }) {
      state.search = payload;
    },

    updataBodyVerify(state, { payload }) {
      for (let index = 0; index < state.clients.length; index++) {
        const element = state.clients[index];
        if (element.g_localId === payload.userId) {
          element.bodyVerify = payload.date;
          break;
        }
      }
    },
  },
});

export const { setCliensData, setScrollY, updataBodyVerify, searchClients, ifSearch } =
  clientsSlice.actions;

export default clientsSlice.reducer;
