import React, { useState } from 'react';
import styles from './AdminClientControl.module.css';

import { updataClient } from '../../firebase';

const AdminClientControl = ({ userId, groupAccompany, individualAccompany, calculated }) => {
  const [fakeInputGroupAccompany, setFakeInputGroupAccompany] = useState(groupAccompany);
  const [fakeInputIndividualAccompany, setFakeInputIndividualAccompany] =
    useState(individualAccompany);
  const [fakeInputCalculated, setFakeInputCalculated] = useState(calculated);

  const ifChecked = (input) => {
    if (input === 'groupAccompany') {
      if (fakeInputGroupAccompany) {
        setFakeInputGroupAccompany(false);
        updataClient({ groupAccompany: false }, userId);
      } else {
        updataClient({ groupAccompany: true }, userId);
        updataClient({ individualAccompany: false }, userId);
        setFakeInputGroupAccompany(true);
        setFakeInputIndividualAccompany(false);
      }
    }
    if (input === 'individualAccompany') {
      if (fakeInputIndividualAccompany) {
        setFakeInputIndividualAccompany(false);
        updataClient({ individualAccompany: false }, userId);
      } else {
        updataClient({ individualAccompany: true }, userId);
        updataClient({ groupAccompany: false }, userId);
        setFakeInputIndividualAccompany(true);
        setFakeInputGroupAccompany(false);
      }
    }

    if (input === 'calculated') {
      if (fakeInputCalculated) {
        updataClient({ calculated: false }, userId);

        setFakeInputCalculated(false);
      } else {
        updataClient({ calculated: true }, userId);
        setFakeInputCalculated(true);
      }
    }
  };

  return (
    <div className={styles.items_wrapper}>
      <div className={styles.item_grup}>
        <div className={styles.item_grup_item}>
           
          <div
            className={
              fakeInputGroupAccompany
                ? styles.faceInputChecked + ' ' + styles.active
                : styles.faceInputChecked
            }
            onClick={() => ifChecked('groupAccompany')}></div>
        </div>
        <div className={styles.item_grup_item}>
           
          <div
            className={
              fakeInputIndividualAccompany
                ? styles.faceInputChecked + ' ' + styles.active
                : styles.faceInputChecked
            }
            onClick={() => ifChecked('individualAccompany')}></div>
        </div>
      </div>

      <div className={styles.item}>
        
        <div
          className={
            fakeInputCalculated
              ? styles.faceInputChecked + ' ' + styles.active
              : styles.faceInputChecked
          }
          onClick={() => ifChecked('calculated')}></div>
      </div>
    </div>
  );
};

export default AdminClientControl;
