import { useState } from 'react';

import styles from './FormRegisterAndLogin.module.css';

const FormRegisterAndLogin = ({ title, handleClick }) => {
  const [email, setEmail] = useState('');
  const [pass, setPass] = useState('');

  return (
    <form className={styles.formInner}>
      <input
        className={styles.form_input}
        type="text"
        required
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <input
        className={styles.form_input}
        type="text"
        required
        value={pass}
        onChange={(e) => setPass(e.target.value)}
      />
      <button className={styles.form_btn} onClick={(e) => handleClick(e, email, pass)}>
        {title}
      </button>
    </form>
  );
};

export default FormRegisterAndLogin;
