import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { setClientBodyData } from '../../store/slices/clientBodyDataSlice';

import { PopupAddBodyItem } from '../../components/PopapAddBodyItem/PopupAddBodyItem';
import { PopupCompare } from '../../components/PopupCompare/PopupCompare';
import { bodyScrollOverflowAuto } from '../../helper';
import { useAddNameAndTel, useAuth } from '../../hooks/use-auth';
import { Navigate } from 'react-router-dom';
import FormAddData from '../../components/FormAddData/FormAddData';
import LoaderPage from '../../components/LoaderPage/LoaderPage';
import LoaderElement from '../../components/LoaderElement/LoaderElement';
import { setUserDataRedux } from '../../store/slices/userSlice';

import styles from './client-page.module.css';

import {
  getFirestore,
  collection,
  query,
  where,
  getDocs,
  doc,
  orderBy,
  updateDoc,
} from 'firebase/firestore';

import { app } from '../../firebase';
import ClientBodyItems from '../../components/ClientBodyItems/ClientBodyItems';
const db = getFirestore(app);

// ====================================================================
const ClientPage = () => {
  // console.log('ClientPage render');
  const dispatch = useDispatch();

  //    
  const [bodyItems, setBodyItems] = useState(true);
  //       
  const [addBodyItem, setAddBodyItem] = useState(false);
  //1)    2)    
  const [ompareArrey, setCompareArrey] = useState([]);
  //   
  const [ignor, setIgnor] = useState(false);
  //     
  const [loader, setLoader] = useState(false);
  const [loaderPage, setLoaderPage] = useState(false);

  const ifAddNameAndTel = useAddNameAndTel();

  const { g_email, g_localId } = useSelector((state) => state.user);
  const { clientBodyData } = useSelector((state) => state.clientBodyData);

  useEffect(() => {
    //        
    if (!addBodyItem && g_email) {
      //    firestore ( )
      const getClientsBodyData = async () => {
        setLoader(true);

        const q = query(
          collection(db, 'clientsBodyData'),

          where('g_email', '==', g_email),
          //    
          // orderBy('time_milliseconds'),
          //    
          orderBy('time_milliseconds', 'desc'),
        );

        const querySnapshot = await getDocs(q);
        const clientBodyDataArrey = [];
        querySnapshot.forEach((doc) => {
          // console.log(doc.id, ' => ', doc.data());
          clientBodyDataArrey.push({ id_doc: doc.id, ...doc.data() });
        });
        // setTimeout(() => {
        dispatch(setClientBodyData(clientBodyDataArrey));
        setLoader(false);
        // }, 5000);
      };
      getClientsBodyData();
    }
  }, [addBodyItem]);

  const ifOpenBodyItems = () => {
    setBodyItems(!bodyItems);
  };

  const addCompare = (item) => {
    if (ompareArrey.length && ompareArrey[0].time_milliseconds === item.time_milliseconds) {
      setCompareArrey([]);
    } else {
      setCompareArrey([...ompareArrey, item]);
    }
  };

  const closedPopupCompare = () => {
    bodyScrollOverflowAuto();

    //    
    setCompareArrey([]);
  };

  const getPageContent = () => {
    return (
      <div className="page">
        <div className="container">
          {/*1 ================================================================================= */}

          <div className={styles.clientPage_wrapper}>
            <div className={styles.body_wrapper}>
              {/* 1.1 ----------------------------------------------------------------------------------- */}
              <div type="button" className={styles.body_btn_wrapper}>
                <button
                  type="button"
                  className={
                    bodyItems ? styles.body_btn_title + ' ' + styles.open : styles.body_btn_title
                  }
                  onClick={() => ifOpenBodyItems()}>
                  <span> </span>

                  {clientBodyData.length ? (
                    <svg viewBox="0 0 15 15" width="14px" height="14px">
                      <path d="M7.158.3.29 6.825a.929.929 0 0 0 0 1.349L7.158 14.7c.422.4 1.087.4 1.508 0a1.07 1.07 0 0 0 0-1.554L2.913 7.68a.247.247 0 0 1 0-.361l5.753-5.466a1.07 1.07 0 0 0 0-1.554 1.096 1.096 0 0 0-1.508 0Z"></path>
                    </svg>
                  ) : (
                    ''
                  )}
                </button>

                <div className={styles.body_btn_svg_wrapper} onClick={() => setAddBodyItem(true)}>
                  <svg
                    version="1.1"
                    id="Layer_1"
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    width="24px"
                    height="24px"
                    viewBox="0 0 64 64"
                    enableBackground="new 0 0 64 64">
                    <g>
                      <path d="M56,0H22.586L8,14.586V64h48V0z M22,3.414V14H11.414L22,3.414z M54,62H10V16h14V2h30V62z" />
                      <polygon points="31,27 31,35 23,35 23,37 31,37 31,45 33,45 33,37 41,37 41,35 33,35 33,27 	" />
                    </g>
                  </svg>
                </div>
              </div>
              {/* 1.1 ----------------------------------------------------------------------------------- */}

              {/* 1.2 ----------------------------------------------------------------------------------- */}
              <div className={styles.body_items_wrapper}>
                {bodyItems && clientBodyData.length ? (
                  <ClientBodyItems
                    items={clientBodyData}
                    addCompare={addCompare}
                    ompareArrey={ompareArrey}
                  />
                ) : (
                  ''
                )}
                {bodyItems && loader ? <LoaderElement /> : ''}
              </div>
              {/* 1.2 ----------------------------------------------------------------------------------- */}
            </div>
          </div>
        </div>

        {/* 1 ================================================================================= */}

        {addBodyItem ? <PopupAddBodyItem setAddBodyItem={setAddBodyItem} /> : ''}
        {ompareArrey.length > 1 ? (
          <PopupCompare closedPopupCompare={closedPopupCompare} ompareArrey={ompareArrey} />
        ) : (
          ''
        )}
      </div>
    );
  };

  //    
  const setUserData = async ({ name, tel }) => {
    if ((name.length, tel.length)) {
      setLoaderPage(true);

      const data = {
        name: name,
        tel: tel,
      };

      const washingtonRef = doc(db, 'clients', g_localId);

      // '     BD
      await updateDoc(washingtonRef, {
        name: data.name,
        tel: data.tel,
      });
      // setTimeout(() => {
      // '    frontend
      dispatch(setUserDataRedux(data));
      setLoaderPage(false);
      // }, 2000);
    }
  };

  return useAuth() ? (
    ifAddNameAndTel || ignor ? (
      getPageContent()
    ) : (
      <div className="container">
        <div className={styles.form_addData_wrapper}>
          <FormAddData title="" handleClick={setUserData} ignorClick={setIgnor} />
          {loaderPage && <LoaderPage />}
        </div>
      </div>
    )
  ) : (
    <Navigate to="/smile/login" />
    // <Navigate to="/login-for-tst" />
  );
};

export default ClientPage;
