import { initializeApp } from 'firebase/app';
import {
  doc,
  updateDoc,
  getFirestore,
  getDoc,
  getDocs,
  query,
  where,
  orderBy,
  limit,
  collection,
} from 'firebase/firestore';

const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKED,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_API_ID,
};

export const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

// =====================================================================

//    
// itemUpdate -         updataClient({ groupAccompany: true }, userId);
// userId -      
export const updataClient = async (itemUpdate, userId) => {
  const clientRef = doc(db, 'clients', userId);

  await updateDoc(clientRef, itemUpdate);
};

//  user  
export const getUser = async (g_localId) => {
  const docRef = doc(db, 'clients', g_localId);
  const docSnap = await getDoc(docRef);

  if (docSnap.exists()) {
    const user = docSnap.data();
    return user;
  } else {
    return false;
  }
};

//  2        
export const getBodyForCompare = async (email) => {
  //          
  const body = query(
    collection(db, 'clientsBodyData'),
    where('g_email', '==', email),
    // orderBy('time_milliseconds'),
    orderBy('time_milliseconds', 'desc'),
    limit(2),
  );
  //         
  const clientBodyArrey = await getDocs(body);
  const clientBody = [];
  clientBodyArrey.forEach((doc) => {
    // console.log(doc.id, ' => ', doc.data());
    clientBody.push({ id_doc: doc.id, ...doc.data() });
  });

  return clientBody;
};
