import React from 'react';

import styles from './AdminClientInfo.module.css';
import { phoneCorrect } from '../../helper';

const AdminClientInfo = ({ name, tel }) => {
  return (
    <div className={styles.items_wrapper}>
      <div className={styles.item}>{name ? name : "'  "}</div>
      <div className={styles.item}>
        {tel ? (
          <a className={styles.tel_connection} href={'tel:+' + tel}>
            {phoneCorrect(tel)}
          </a>
        ) : (
          '.  '
        )}
      </div>
    </div>
  );
};

export default AdminClientInfo;
