import { useNavigate } from 'react-router-dom';
import styles from './LoginGoogle.module.css';
import { getAuth, signInWithPopup, GoogleAuthProvider } from 'firebase/auth';
import { useDispatch } from 'react-redux';
import { isAdminTrue, setUser } from '../store/slices/userSlice';

import { getFirestore, doc, setDoc, getDoc } from 'firebase/firestore';
import { app, getUser } from '../firebase';
import { ifAdmin } from '../hooks/use-auth';
import AdminOrClient from './AdminOrClient/AdminOrClient';
import { useState } from 'react';
import Alert from './Alert/Alert';

const provider = new GoogleAuthProvider();
const db = getFirestore(app);

const LoginGoogle = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [whatRender, setWhatRender] = useState(false);
  const [alertError, setAlertError] = useState(false);

  //    email admin      page
  const whichRedirectPage = (user, userId) => {
    if (user === 'admin') {
      navigate(`/smile`);
      dispatch(isAdminTrue());
    } else {
      navigate(`/smile/client/${userId}`);
    }
  };

  //     page
  const redirectPage = (email, userId) => {
    if (ifAdmin(email)) {
      setWhatRender(true);
    } else {
      navigate(`/smile/client/${userId}`);
    }
  };

  //  user  redax
  const addUserToRedax = (user) => {
    dispatch(
      setUser({
        g_email: user.g_email,
        name: user.name,
        tel: user.tel,
        avatar_url: user.avatar_url,
        g_localId: user.g_localId,

        date: user.date,
        date_day: user.date_day,
        date_time: user.date_time,
        time_milliseconds: user.time_milliseconds,

        calculated: user.calculated,
        individualAccompany: user.individualAccompany,
        groupAccompany: user.groupAccompany,

        bodyVerify: user.bodyVerify,
        bodyUpdata: user.bodyUpdata,

        sortValue: user.sortValue,
      }),
    );
  };

  //     BD
  const addNewClientBD = async (user) => {
    let sortValue = {};
    if (ifAdmin(user.email)) {
      sortValue = { sort: 'name', filter: 'name', name: '  ' };
    }
    await setDoc(doc(db, 'clients', user.localId), {
      date: new Date().toLocaleDateString(), //   
      date_time: new Date().toLocaleTimeString(), //   
      date_day: new Date().getDay(), //   
      time_milliseconds: new Date().getTime(), //    1  1970 
      name: '', //'
      tel: '', // 
      g_fullName: user.fullName,
      g_email: user.email,
      g_localId: user.localId,
      avatar_url: '',
      groupAccompany: false, //   
      individualAccompany: false, //   
      calculated: false, // 
      bodyVerify: new Date().getTime(),
      bodyUpdata: new Date().getTime(),
      notes: '', //  
      sortValue: sortValue,
    });
  };

  const goUser = async (user) => {
    //      DB
    const userDB = await getUser(user.localId);
    // j user  redax
    addUserToRedax(userDB);
    //     page
    redirectPage(userDB.g_email, userDB.g_localId);
  };

  const handlerLogin = () => {
    const auth = getAuth();
    signInWithPopup(auth, provider)
      .then((result) => {
        const user = result._tokenResponse;

        //  user   
        if (user.isNewUser) {
          //   client  BD
          addNewClientBD(user);
          goUser(user);
        } else {
          goUser(user);
        }
      })
      .catch((error) => {
        // console.log('error --- ', error);
        setAlertError(true);
      });
  };
  return (
    <>
      <button className={styles.btn} onClick={handlerLogin}>
        <div className={styles.btn_content_wrapper}>
          <svg width="52" height="52" role="img">
            <title>Google's Logo</title>
            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
              <rect x="0" y="0" width="52" height="52" rx="2"></rect>
              <g
                id="logo_googleg_48dp"
                transform="translate(13.65, 13.65) scale(1.4300000000000002)">
                <path
                  d="M17.64,9.20454545 C17.64,8.56636364 17.5827273,7.95272727 17.4763636,7.36363636 L9,7.36363636 L9,10.845 L13.8436364,10.845 C13.635,11.97 13.0009091,12.9231818 12.0477273,13.5613636 L12.0477273,15.8195455 L14.9563636,15.8195455 C16.6581818,14.2527273 17.64,11.9454545 17.64,9.20454545 L17.64,9.20454545 Z"
                  id="Shape"
                  fill="#4285F4"></path>
                <path
                  d="M9,18 C11.43,18 13.4672727,17.1940909 14.9563636,15.8195455 L12.0477273,13.5613636 C11.2418182,14.1013636 10.2109091,14.4204545 9,14.4204545 C6.65590909,14.4204545 4.67181818,12.8372727 3.96409091,10.71 L0.957272727,10.71 L0.957272727,13.0418182 C2.43818182,15.9831818 5.48181818,18 9,18 L9,18 Z"
                  id="Shape"
                  fill="#34A853"></path>
                <path
                  d="M3.96409091,10.71 C3.78409091,10.17 3.68181818,9.59318182 3.68181818,9 C3.68181818,8.40681818 3.78409091,7.83 3.96409091,7.29 L3.96409091,4.95818182 L0.957272727,4.95818182 C0.347727273,6.17318182 0,7.54772727 0,9 C0,10.4522727 0.347727273,11.8268182 0.957272727,13.0418182 L3.96409091,10.71 L3.96409091,10.71 Z"
                  id="Shape"
                  fill="#FBBC05"></path>
                <path
                  d="M9,3.57954545 C10.3213636,3.57954545 11.5077273,4.03363636 12.4404545,4.92545455 L15.0218182,2.34409091 C13.4631818,0.891818182 11.4259091,0 9,0 C5.48181818,0 2.43818182,2.01681818 0.957272727,4.95818182 L3.96409091,7.29 C4.67181818,5.16272727 6.65590909,3.57954545 9,3.57954545 L9,3.57954545 Z"
                  id="Shape"
                  fill="#EA4335"></path>
                <path d="M0,0 L18,0 L18,18 L0,18 L0,0 Z" id="Shape"></path>
              </g>
            </g>
          </svg>
          <span>Google</span>
        </div>
      </button>
      {whatRender && <AdminOrClient handleClick={whichRedirectPage} />}
      {alertError && (
        <Alert
          text="  !    !"
          handleClick={setAlertError}
        />
      )}
    </>
  );
};

export default LoginGoogle;
