import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  clientBodyData: [],
};

const clientBodyDataSlice = createSlice({
  name: 'clientBodyData',
  initialState,
  reducers: {
    setClientBodyData(state, action) {
      state.clientBodyData = action.payload;
    },
    deleteClientBodyData(state, action) {
      const result = state.clientBodyData.filter((item) => item.id_doc !== action.payload);
      state.clientBodyData = result;
    },
    resetClientBody(state) {
      state.clientBodyData = [];
    },
  },
});

export const { setClientBodyData, deleteClientBodyData, resetClientBody } =
  clientBodyDataSlice.actions;

export default clientBodyDataSlice.reducer;
