import React, { useEffect, useState } from 'react';
import styles from './Sort.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { setSortValue } from '../../store/slices/userSlice';
import { updataClient } from '../../firebase';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden } from '../../helper';
import LoaderPage from '../LoaderPage/LoaderPage';

const Sort = () => {
  const dispatch = useDispatch();

  const [showSort, setShowSort] = useState(false);
  const [loader, setLoader] = useState(false);

  const sortValue = useSelector((state) => state.user.sortValue);
  const userIdRedux = useSelector((state) => state.user.g_localId);

  const setValue = async (value, userId) => {
    setLoader(true);
    dispatch(setSortValue(value));
    await updataClient({ sortValue: value }, userId);
    setShowSort(false);
    bodyScrollOverflowAuto();
    setLoader(false);
  };

  const showHidenSortMenu = () => {
    setShowSort(!showSort);
    if (!showSort) {
      bodyScrollOverflowHidden();
    } else {
      bodyScrollOverflowAuto();
    }
  };
  const hiddenSortMenu = () => {
    setShowSort(false);
    bodyScrollOverflowAuto();
  };

  return (
    <div className={styles.sort_wrapper} id="sort_wrapper">
      <button
        className={showSort ? styles.sort_btn + ' ' + styles.open : styles.sort_btn}
        onClick={() => showHidenSortMenu()}>
        <span className={styles.sort_btn_value}>{sortValue.name}</span>
        <svg viewBox="0 0 15 15" width="14px" height="14px">
          <path d="M7.158.3.29 6.825a.929.929 0 0 0 0 1.349L7.158 14.7c.422.4 1.087.4 1.508 0a1.07 1.07 0 0 0 0-1.554L2.913 7.68a.247.247 0 0 1 0-.361l5.753-5.466a1.07 1.07 0 0 0 0-1.554 1.096 1.096 0 0 0-1.508 0Z"></path>
        </svg>
      </button>

      {showSort && (
        <>
          <div className={styles.sort_content_wrapper}>
            <div
              className={
                sortValue.filter === 'name'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'name', filter: 'name', name: '  ' }, userIdRedux)
              }>
                
            </div>
            <div
              className={
                sortValue.filter === 'groupAccompanyIndividualAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  {
                    sort: 'name',
                    filter: 'groupAccompanyIndividualAccompany',
                    name: '   ',
                  },
                  userIdRedux,
                )
              }>
                 
            </div>

            <div
              className={
                sortValue.filter === 'groupAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'name', filter: 'groupAccompany', name: ' ' }, userIdRedux)
              }>
               
            </div>

            <div
              className={
                sortValue.filter === 'individualAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  {
                    sort: 'name',
                    filter: 'individualAccompany',
                    name: ' ',
                  },
                  userIdRedux,
                )
              }>
               
            </div>
            <div
              className={
                sortValue.filter === 'calculated'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'name', filter: 'calculated', name: '' }, userIdRedux)
              }>
              
            </div>
            <div
              className={
                sortValue.filter === 'noCalculated'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  {
                    sort: 'name',
                    filter: 'noCalculated',
                    name: '    ',
                  },
                  userIdRedux,
                )
              }>
                  
            </div>
            <div
              className={
                sortValue.filter === 'noAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  { sort: 'name', filter: 'noAccompany', name: '   ' },
                  userIdRedux,
                )
              }>
                
            </div>
            <div
              className={
                sortValue.filter === 'deteNew'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  { sort: 'newClient', filter: 'deteNew', name: '  ( )' },
                  userIdRedux,
                )
              }>
                ( )
            </div>

            <div
              className={
                sortValue.filter === 'deteOld'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue(
                  { sort: 'oldClient', filter: 'deteOld', name: '  ( )' },
                  userIdRedux,
                )
              }>
                ( )
            </div>
            {loader && <LoaderPage />}
          </div>
          <div className={styles.sort_fon}></div>
        </>
      )}
    </div>
  );
};

export default Sort;
