import React, { useEffect, useState } from 'react';
import styles from './AdminClientBody.module.css';
import { useDispatch, useSelector } from 'react-redux';
import {
  deleteClientBodyData,
  resetClientBody,
  setClientBodyData,
} from '../../store/slices/clientBodyDataSlice';
import {
  collection,
  deleteDoc,
  doc,
  getDocs,
  getFirestore,
  orderBy,
  query,
  where,
} from 'firebase/firestore';

import { app } from '../../firebase';
import { PopupCompare } from '../PopupCompare/PopupCompare';
import { bodyScrollOverflowAuto } from '../../helper';
import LoaderElement from '../LoaderElement/LoaderElement';
const db = getFirestore(app);

const AdminClientBody = ({ email }) => {
  const dispatch = useDispatch();

  //    
  const [bodyItem, setBodyItem] = useState(null);
  //1)    2)    
  const [ompareArrey, setCompareArrey] = useState([]);
  //
  const [loaderItem, setLoaderItem] = useState(null);
  const [loaderElement, setLoaderElement] = useState(false);

  const { clientBodyData } = useSelector((state) => state.clientBodyData);

  useEffect(() => {
    //    firestore ( )
    const getClientBodyData = async () => {
      setLoaderElement(true);
      const q = query(
        collection(db, 'clientsBodyData'),

        where('g_email', '==', email),
        //    
        // orderBy('time_milliseconds'),
        //    
        orderBy('time_milliseconds', 'desc'),
      );

      const querySnapshot = await getDocs(q);
      const clientBodyDataArrey = [];
      querySnapshot.forEach((doc) => {
        // console.log(doc.id, ' => ', doc.data());
        clientBodyDataArrey.push({ id_doc: doc.id, ...doc.data() });
      });
      // setTimeout(() => {
      dispatch(setClientBodyData(clientBodyDataArrey));
      setLoaderElement(false);
      // }, 5000);
    };
    getClientBodyData();

    return () => {
      dispatch(resetClientBody());
    };
  }, []);

  //    
  const deleteItemBody = async (item) => {
    setLoaderItem(item.time_milliseconds);

    var answer = window.confirm('  ?');
    if (answer) {
      //   firebase
      await deleteDoc(doc(db, 'clientsBodyData', item.id_doc));
      //   redux
      // setTimeout(() => {
      dispatch(deleteClientBodyData(item.id_doc));
      setLoaderItem(null);
      // }, 5000);
    } else {
      setLoaderItem(null);
    }
  };

  const ifOpenBodyItem = (id) => {
    if (bodyItem === id) {
      setBodyItem(null);
    } else {
      setBodyItem(id);
    }
  };

  const addCompare = (item) => {
    if (ompareArrey.length && ompareArrey[0].time_milliseconds === item.time_milliseconds) {
      setCompareArrey([]);
    } else {
      setCompareArrey([...ompareArrey, item]);
    }
  };

  const closedPopupCompare = () => {
    bodyScrollOverflowAuto();
    //    
    setCompareArrey([]);
  };

  const getBodyItem = (items) => {
    return items.map((item) => {
      return (
        <div key={item.time_milliseconds} className={styles.body_item_wrapper}>
          <div className={styles.body_btn_wrapper}>
            <button
              className={
                bodyItem === item.time_milliseconds
                  ? styles.body_btn + ' ' + styles.open
                  : styles.body_btn
              }
              type="button"
              onClick={() => ifOpenBodyItem(item.time_milliseconds)}>
              <div className={styles.data_wrapper}>
                <span>{item.date}</span>
                <span className={styles.body_time}>{item.date_time}</span>
              </div>
              <svg viewBox="0 0 15 15" width="14px" height="14px">
                <path d="M7.158.3.29 6.825a.929.929 0 0 0 0 1.349L7.158 14.7c.422.4 1.087.4 1.508 0a1.07 1.07 0 0 0 0-1.554L2.913 7.68a.247.247 0 0 1 0-.361l5.753-5.466a1.07 1.07 0 0 0 0-1.554 1.096 1.096 0 0 0-1.508 0Z"></path>
              </svg>
            </button>
            <div className={styles.body_control_wrapper}>
              {ompareArrey.length &&
              ompareArrey[0].time_milliseconds === item.time_milliseconds ? (
                <div className={styles.btn_compare_alert}>{ompareArrey.length}</div>
              ) : (
                ''
              )}
              <button
                type="button"
                className={
                  ompareArrey.length && ompareArrey[0].time_milliseconds > item.time_milliseconds
                    ? styles.body_control_btn_compare + ' ' + 'disabled'
                    : styles.body_control_btn_compare
                }
                onClick={() => addCompare(item)}>
                
              </button>
              <button
                type="button"
                className={
                  loaderItem
                    ? styles.body_control_delete_btn + ' ' + 'disabled'
                    : styles.body_control_delete_btn
                }
                onClick={() => deleteItemBody(item)}>
                <svg
                  fill="#fff"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  width="20px"
                  height="20px"
                  viewBox="0 0 900.5 900.5">
                  <g>
                    <path
                      d="M176.415,880.5c0,11.046,8.954,20,20,20h507.67c11.046,0,20-8.954,20-20V232.487h-547.67V880.5L176.415,880.5z
             M562.75,342.766h75v436.029h-75V342.766z M412.75,342.766h75v436.029h-75V342.766z M262.75,342.766h75v436.029h-75V342.766z"
                    />
                    <path
                      d="M618.825,91.911V20c0-11.046-8.954-20-20-20h-297.15c-11.046,0-20,8.954-20,20v71.911v12.5v12.5H141.874
            c-11.046,0-20,8.954-20,20v50.576c0,11.045,8.954,20,20,20h34.541h547.67h34.541c11.046,0,20-8.955,20-20v-50.576
            c0-11.046-8.954-20-20-20H618.825v-12.5V91.911z M543.825,112.799h-187.15v-8.389v-12.5V75h187.15v16.911v12.5V112.799z"
                    />
                  </g>
                </svg>
              </button>
            </div>
            {loaderItem === item.time_milliseconds ? <LoaderElement /> : ''}
          </div>
          {/* ================================================= */}
          {bodyItem === item.time_milliseconds ? (
            <div className={styles.body_info}>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}> ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.kg}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.breastVolumes}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.handVolumes}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.waistVolumes}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.abdominalVolumes}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.thighVolumes}></input>
              </div>
              <div className={styles.body_item}>
                <div className={styles.body_item_title}>'  ()</div>
                <input
                  className={styles.body_item_input}
                  type="number"
                  readOnly
                  defaultValue={item.legVolumes}></input>
              </div>
            </div>
          ) : (
            ''
          )}
          {ompareArrey.length > 1 ? (
            <PopupCompare closedPopupCompare={closedPopupCompare} ompareArrey={ompareArrey} />
          ) : (
            ''
          )}
        </div>
      );
    });
  };

  return loaderElement ? (
    <div className={styles.loader_wrapper}>
      <LoaderElement />
    </div>
  ) : clientBodyData.length ? (
    getBodyItem(clientBodyData)
  ) : (
    <div className={styles.body_empty}> :)</div>
  );
};

export default AdminClientBody;
