import { useSelector } from 'react-redux';

//  user  (      "use")
export function useAuth() {
  const { g_email } = useSelector((state) => state.user);

  return !!g_email;
}

//   email   user    (       "use")
export function useIfAdmin() {
  const { g_email } = useSelector((state) => state.user);
  if (
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL_2 ||
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL
  ) {
    return true;
  } else {
    return false;
  }
}

//    email  
export function ifAdmin(g_email) {
  if (
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL_2 ||
    g_email === process.env.REACT_APP_FIREBASE_ADMIN_EMAIL
  ) {
    return true;
  } else {
    return false;
  }
}

//       (       "use")
export function useAddNameAndTel() {
  const { name, tel } = useSelector((state) => state.user);
  let ifAddNameAndTel = false;
  if (name.trim().length > 2 && tel.trim().length === 12) {
    ifAddNameAndTel = true;
  } else {
    ifAddNameAndTel = false;
  }

  return ifAddNameAndTel;
}
